import IDataResponse from '../dataAccess/IDataResponse';

/**
 * Represents the base data response.
 */
class BaseDataResponse implements IDataResponse {
  public requestId: string;

  /**
   * Initializes a new instance of BaseDataResponse
   * @param requestId id that initiated the response
   */
  constructor(requestId: string) {
    this.requestId = requestId;
  }
}

export default BaseDataResponse;
